import Page from './components/page';


const ivue = {
    Page
};

const install = function (Vue, opts = {}) {
    Object.keys(ivue).forEach((key) => {
        Vue.component(key, ivue[key]);
    });
};


if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

module.exports = Object.assign(ivue, {install});



